.about {
	padding-top: 40px;
	padding-bottom: 40px;
}

.about__image img {
	width: 100%;
}

.about__image {
	border: 3px solid $bgColor;
	background-color: $bgColor-3;
	border-radius: 10px;
	width: 280px;
	margin: 0 auto;
}

.about__title {
	margin-top: 20px;
	padding: 10px;
	background-color: $bgColor;
	@include setFont(18px, 24px, bold, #fff);
	border-radius: 10px;
}

.about__description {
	margin-top: 20px;
	@include setFont(16px, 22px, normal, $textColor);
}

.about__item {
	position: relative;
	padding-left: 40px;
	margin-bottom: 10px;

	&::before {
		content: '';
		position: absolute;
		top: 3px;
		left: 0;

		width: 15px;
		height: 15px;
		background-color: $bgColor;
		border-radius: 5px;
	}
}

@media (min-width: 660px) {
	.about__image {
		width: 50%;
		margin-right: 60px;
	}

	.about__title {
		margin-top: 0;
	}

	.about .wrapper {
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
	}

	.about__container {
		width: 50%;
	}
}

@media (min-width: 960px) {
	.about {
		padding-top: 80px;
		padding-bottom: 80px;
	}

	.about .wrapper {
		align-items: flex-start;
	}

	.about__container {
		width: 50%;
	}

	.about__title {
		margin-top: 0;
		padding: 15px;
		background-color: $bgColor;
		@include setFont(24px, 32px, bold, #fff);
	}

	.about__description {
		margin-top: 40px;
		@include setFont(20px, 26px, normal, $textColor);
	}

	.about__item {
		padding-left: 80px;
		margin-bottom: 20px;

		&::before {
			content: '';
			position: absolute;
			top: 3px;
			left: 0;

			width: 45px;
			height: 15px;
			background-color: $bgColor;
			border-radius: 5px;
		}
	}
}