.feedback {
	padding-top: 20%;

	& .wrapper {
		display: flex;
		flex-direction: column;
		align-items: center;
	}
}

.feedback__description {
	@include setFont(22px, 26px, bold, $textColor);
	text-align: center;
}

.feedback__form {
	margin-top: 40px;
}

@media (min-width: 660px) {
	.feedback {
		padding-top: 10%;
	}
}

@media (min-width: 960px) {
	.feedback {
		padding-top: 100px;
	}
}