  background-color: #2F4F4F; }

.navigation--opened .navigation__toggle::before {
  transform: rotate(45deg); }

.navigation--opened .navigation__toggle::after {
  transform: rotate(-45deg); }

.navigation--closed .navigation__list {
  display: none; }

.navigation--opened {
